<template>
  <q-page padding>
    <!-- Week Navigation and Date Selector -->
    <div class="row items-center justify-between q-mb-md">
      <q-space/>
      <q-btn dense color='secondary' icon="chevron_left" @click="selectPreviousDay" />
      <q-btn class='q-mx-sm q-px-lg' style="width:220px;" dense no-caps color='primary' :label="selectedDayDisplayName" @click="showDatePicker = true"/>
      <q-btn dense color='secondary' icon="chevron_right" @click="selectNextDay" />
      <q-space/>
    </div>


    <!-- Date Picker Dialog -->
    <q-dialog v-model="showDatePicker">
      <q-card >
        <q-card-section>
          <q-date
            v-model="selectedDate"
            mask="YYYY-MM-DD"
            first-day-of-week="1"
            minimal
            @input="showDatePicker = false"
          />
        </q-card-section>
        <q-card-actions align="right">
          <q-btn label="OK" color="primary" @click="showDatePicker = false"/>
        </q-card-actions>
      </q-card>
    </q-dialog>

    <!-- Checkbox for Einzelbucher -->
    <div class="row items-center q-mb-md">
      <q-checkbox v-model="isSingleBooker" label="Rabatt für Einzelbucher:innen" @click="fetchBookingSchedule" />
    </div>

    <!-- Scroll Area for Horizontal Scrolling -->
    <q-table
      ref="bookingTable"
      :rows="tableRows"
      :columns="columns"
      row-key="hour"
      flat
      class="booking-table"
      style="height: 70vh"
      dense
      bordered
      hide-pagination
      :rows-per-page-options="[0]"
    >
    <template v-slot:body="props">
      <q-tr :props="props">
        <q-td v-for="col in props.cols" :key="col.name" :props="props">
          <template v-if="col.field !== 'hour'">

            <q-btn v-if="col.value !== 'belegt'" size='sm' dense color="blue" @click="openBookingRequest(props.row, col.name)" :label="col.value" style="width: 50px;">
              <q-badge v-if="col.value == '19,00€'" rounded color="orange" floating transparent>%</q-badge>
            </q-btn>

            <q-badge v-else color="grey" :label="col.value" />
          </template>
          <template v-else>
            {{ col.value }}
          </template>
        </q-td>
      </q-tr>
    </template>
    </q-table>
  </q-page>
</template>

<script>
import { date } from 'quasar';
import axios from 'axios';


export default {
  data() {
    return {
      bookingSchedule: {},
      selectedDate: null,
      weekDays: [],
      showDatePicker: false,
      isSingleBooker: false, // Checkbox state
    };
  },
  computed: {
    selectedDayDisplayName() {
      return date.formatDate(this.selectedDate, 'dddd, DD.MM.YYYY', {
        days: ['Sonntag', 'Montag', 'Dienstag', 'Mittwoch', 'Donnerstag', 'Freitag', 'Samstag']
      });
    },
    columns() {
      const cols = [
        { name: 'hour', label: 'Zeit', align: 'left', field: 'hour', format: val => `${val} Uhr`, },
      ];

      this.weekDays.forEach(day => {
        cols.push({
          name: day,
          label: date.formatDate(day, 'dd, DD.MM', { days: ['So', 'Mo', 'Di', 'Mi', 'Do', 'Fr', 'Sa'] }),
          align: 'center',
          format: (val) => { return val === null ? 'belegt' : `${val.toLocaleString('de-DE', { minimumFractionDigits: 2, maximumFractionDigits: 2 })}€`; },
          field: day,
        });
      });

      return cols;
    },
    tableRows() {

      const rows = [];
      for (let hour = 0; hour < 24; hour++) {
        const row = { hour: `${hour}:00` };
        this.weekDays.forEach(day => {


          if (this.bookingSchedule['days'][day] && this.bookingSchedule['days'][day]['schedule'][hour] !== undefined) {
            if (this.bookingSchedule['days'][day]['schedule'][hour] != null) {
              row[day] = this.bookingSchedule['days'][day]['schedule'][hour]
            }
            else {
              row[day] = null;
            }
          } else {
            row[day] = null;
          }

        });
        rows.push(row);
      }
      return rows;
    }
  },
  methods: {
    scrollToBottom() {
      this.$nextTick(() => {
        const tableBody = this.$refs.bookingTable.$el.querySelector('.q-table__middle');
        if (tableBody) {
          tableBody.scrollTop = tableBody.scrollHeight;
        }
      });
    },
    selectPreviousDay() {
      const newDate = date.addToDate(this.selectedDate, { days: -1 });
      this.selectedDate = date.formatDate(newDate, 'YYYY-MM-DD');
    },
    selectNextDay() {
      const newDate = date.addToDate(this.selectedDate, { days: 1 });
      this.selectedDate = date.formatDate(newDate, 'YYYY-MM-DD');
    },
    extractDatesFromResponse(response) {
      if (!response || !response.days) {
        throw new Error("Invalid response structure: 'days' property is missing.");
      }

      // Extract all dates from the 'days' object
      const datesArray = Object.keys(response.days);

      return datesArray;
    },
    async fetchBookingSchedule() {
      try {
        const url = `${process.env.SRV_API_URL}/api/v1/bookings`;

        const response = await axios.get(url, {
          params: {
            start_date: this.selectedDate,
            num_days: 3, 
            single_user: this.isSingleBooker 
          }
        });

        this.weekDays = this.extractDatesFromResponse(response.data);
        this.bookingSchedule = response.data;
      } catch (error) {
        console.error("Error fetching booking schedule:", error);
      }
    },
    openBookingRequest(row, colName) {
      this.isSingleBooker;
      const date = colName;
      // get booking hour from first column of selected row
      const hour = row.hour.split(':')[0];

      this.$router.push({ name: 'booking-request', params: { bookingDate: date, bookingHour: hour, isSingleBooker: this.isSingleBooker } });
    },
  },
  mounted() {
    this.scrollToBottom();
  },
  watch: {
    selectedDate(newDate) {
      this.fetchBookingSchedule(); // Fetch new data when date changes
    }
  },
  created() {
    this.selectedDate = date.formatDate(new Date(), 'YYYY-MM-DD');
    this.fetchBookingSchedule(); // Initial data fetch
  }
};
</script>

<style lang="scss">
.booking-table {
  .q-table__top,
  .q-table__bottom,
  thead tr:first-child th {
    background-color: $primary;
  }
  thead tr th {
    position: sticky;
    z-index: 1;
  }
  thead tr:first-child th {
    top: 0;
  }
}
</style>